                    p.mb-3 Posteriormente el navegador del usuario que visita una pgina le solicita ese cdigo a <i>analytics</i>. Acto seguido la plataforma lo enva y se guarda en un fichero.
                    p.mb-3 <b>Una vez se almacena la informacin, el cdigo crea <i>cookies</i> en el computador o dispositivo mvil del usuario.</b>
                    p.mb-0 Una vez estn definidas las <i>cookies</i>, el cdigo enva la informacin al servidor de <em>Google Analytics</em> con la solicitud de un fichero GIF invisible. Esta informacin es almacenada en otro fichero denominado fichero <i>logs</i> y crea un apartado de datos en el mismo por cada pgina visitada.
