    p.mb-4 El diagrama de flujo corresponde al desarrollo de actividades es una forma grfica de representar un algoritmo o un proceso que estn estructurados e incorporados que pueden ser analizados. La aplicacin grfica en este proceso implementa una variedad de formas geomtricas que identifican cada paso. Estas formas seleccionadas se enlazan a travs de flechas y lneas que comunican la direccin del flujo y definen el trayecto del proceso. <b>Los diagramas de flujo estn representados en cuatro (4) modos.</b>

