    p.mb-4(data-aos="fade-right") Los diagramas de flujo es una forma de control y descripcin de tareas que ayudan a una mejor organizacin, anlisis y reorganizacin de seguimiento de tareas y procesos de caractersticas diferentes, gracias a su facilidad y versatilidad. Tradicionalmente son implementadas en reas financieras, informticas, industriales, etc. <b>A continuacin se podr conocer los elementos que tiene un diagrama: </b>

