    p.mb-4(data-aos="fade-right") Los diagramas de flujo son una forma prctica para explicar un algoritmo, implementar un diagrama para describir la forma lgica internamente en un programa previamente de iniciar la codificacin en proceso autorizado. Es una forma de estructurar un punto de vista general y brindar un modelo a partir del inicio que se requiere codificar. 

