              p.mb-4 Frecuentemente los desarrolladores pueden redactar un Pseudocdigo, una mezcla de lenguaje natural y lenguaje informtico, que logre ser ledo por los usuarios. Esto puede autorizar ms detalle que el diagrama de flujo y ayudar como cambio del diagrama de flujo o como el siguiente procedimiento del cdigo.
